const fs = require('fs');
const IO = require('./io');
const Promise = require('bluebird');

/**
 * mojangmanifest
 * @param {boolean} mojangmanifest
 */
function _fetchManifest(url, local) {
	return new Promise((resolve, reject) => {
		if(fs.existsSync(local)){
			// JSONjavascript
			resolve(JSON.parse(IO.readFileSync(local)));
		} else {
			var manifest;
			IO.request(url).then((text) => {
				manifest = text;
				// 
				return IO.writeFile(local, manifest);
			}).then(() => {
				resolve(JSON.parse(manifest));
			});
		}
	});
}

function _transfer(manifest) {
	var versions = manifest.versions;
	var i = 0;
	var len = versions.length;
	for (; i < len; i++) {
		var version = versions[i];
		version.typeName = _transferType(version.type);
	}
}

function _transferType(type) {
	switch (type){
		case 'release'  : return '';
		case 'snapshot' : return '';
		case 'old_beta' : return '';
		case 'old_alpha': return '';
		default         : return type;
	}
}

class Manifest {

	constructor(manifest) {
		this.manifest = manifest;
	}

	/**
	 * 
	 * @return {array} 
	 */
	getAllVersions() {
		if(this.manifest === null){
			return [];
		}
		return this.manifest.versions;
	}

	/**
	 * 
	 * @return {object} 
	 * @example
	 *  => "v0.0.1":{
	 * 		type:'beta',
	 *   	json:'https://xxx.json'
	 * }
	 */
	getFormatVersions() {
		var list = {};
		this.getAllVersions().map(item => {
			list[item.id] = {
				type: item.type,
				json: item.url
			};
		});
		return list;
	}

	/**
	 * 
	 * @return {object} snapshotrelease
	 */
	getLatest() {
		if(this.manifest === null){
			return {};
		}
		return this.manifest.latest;
	}
}

module.exports = {
	/**
	 * Manifest
	 * @param  {string} url   mojangmanifestURL
	 * @param  {string} local 
	 * @return {object}       Manifest
	 */
	init: (url, local) => {
		return new Promise((resolve, reject) => {
			_fetchManifest(url, local).then(manifest => {
				_transfer(manifest);
				resolve(new Manifest(manifest));
			}).catch(err => {
				reject(err);
			});
		});
	}
}



